import React, { useEffect, useState } from 'react';

import { Tour } from '@enactive-ac/api/src/domain/Tour';

import { Analytics } from './components/Analytics';
import { ThemeContainer } from './components/ThemeContainer';
import { TourSequence } from './components/TourSequence';
import { useTourService } from './services/DependenciesProvider';
import { useEnactiveMetadata } from './services/MetadataProvider';

const App: React.FC = () => {
  const metadata = useEnactiveMetadata();
  const tourService = useTourService();
  const [tour, setTour] = useState<Tour>();

  useEffect(() => {
    void (async () => {
      if (tour === undefined) {
        setTour(await tourService.getTour(metadata.context));
      }
    })();
  }, [tourService, metadata, tour]);

  return (
    <>
      <Analytics />
      {tour !== undefined ? (
        <ThemeContainer>
          <TourSequence
            onAction={(stepId, _, action) =>
              tourService.collectTourAction(
                metadata.context,
                tour.id,
                stepId,
                action,
              )
            }
            onComplete={() => setTour(undefined)}
            tour={tour.steps}
          />
        </ThemeContainer>
      ) : null}
    </>
  );
};

export default App;
