import { createContext, ReactNode, useContext, useMemo } from 'react';

import { EventContext } from '@enactive-ac/api/src/domain/EventContext';
import { InitRequest, InitResponse } from '@enactive-ac/api/src/domain/Init';
import { DefaultTheme, ThemeProvider } from '@mui/system';
import { AxiosInstance } from 'axios';

import { resolveEventContext } from '../domain/EnactiveMetadata';
import { useMemoAsync } from '../hooks/useMemoAsync';
import { RecursivePartial } from '../util/RecursivePartial';
import { useInitService } from './DependenciesProvider';
import { useEnactiveMetadata } from './MetadataProvider';

export class InitService {
  constructor(private readonly axios: AxiosInstance) {}

  async init(
    context: RecursivePartial<EventContext> | undefined,
  ): Promise<InitResponse> {
    const req: InitRequest = { context: resolveEventContext(context) };
    return (await this.axios.post<InitResponse>('init', req)).data;
  }
}

const InitContext = createContext<InitResponse>(
  undefined as unknown as InitResponse,
);

export type InitProviderProps = {
  children: ReactNode;
};
export const InitProvider: React.FC<InitProviderProps> = ({ children }) => {
  const metadata = useEnactiveMetadata();
  const initService = useInitService();
  const [init] = useMemoAsync(
    () => initService.init(metadata.context),
    [initService, metadata],
  );
  const theme = useMemo<DefaultTheme>(
    () => ({
      palette: {
        primary: {
          contrastText: init?.site.theme?.primaryContrastText ?? '#fff',
          main: init?.site.theme?.primaryColor ?? '#4f4f4f',
        },
      },
    }),
    [init],
  );
  if (init !== undefined) {
    return (
      <InitContext.Provider value={init}>
        <ThemeProvider theme={theme}>{children}</ThemeProvider>
      </InitContext.Provider>
    );
  } else {
    return null;
  }
};

export function useSite() {
  return useContext(InitContext).site;
}
